.page {
  min-width: 320px;
  height: 100%;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  color: #1d1613;
  background-color: #ffffff;
  scroll-behavior: smooth;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
}
@media (max-width: 1023px) {
  .page__body--modal-open {
    overflow: hidden;
  }
}

.page__main {
  flex-grow: 2;
}
@media (max-width: 1023px) {
  .page__main--nav-open {
    display: none;
  }
}

.button {
  font-size: 16px;
  line-height: 20.8px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 68px;
  padding: 5px;
  box-sizing: border-box;
  background-color: #a87b62;
  font-weight: 500;
  color: #ffffff;
  border: 1px solid #a87b62;
  outline: none;
  cursor: pointer;
}
.button:hover, .button:focus {
  background-color: rgba(168, 123, 98, 0.9);
}
.button:active {
  background-color: rgba(168, 123, 98, 0.7);
}
.button--transparent {
  color: #1d1613;
  background-color: transparent;
}
.button--transparent:hover, .button--transparent:focus {
  background-color: transparent;
  outline: 1px solid #a87b62;
}
.button--transparent:active {
  background-color: transparent;
  border: 1px solid rgba(168, 123, 98, 0.8);
  outline: 1px solid rgba(168, 123, 98, 0.8);
}

.page-header {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.page-header__inner {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  max-width: 1200px;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 66px 1fr;
  width: 100%;
  padding-top: 30px;
  padding-bottom: 18px;
}
@media (max-width: 1023px) {
  .page-header__inner {
    padding-left: 45px;
    padding-right: 45px;
    padding-bottom: 29px;
  }
}
@media (max-width: 767px) {
  .page-header__inner {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 20px;
    padding-bottom: 19px;
  }
}

.page-header__button-box {
  display: none;
}
@media (max-width: 1023px) {
  .page-header__button-box {
    display: block;
    line-height: 0;
  }
}

.page-header__menu-button {
  width: 24px;
  height: 18px;
  position: relative;
  margin-top: 24px;
  padding: 0;
  border-top: 2px solid #000000;
  border-bottom: 2px solid #000000;
  border-left: none;
  border-right: none;
  background-color: transparent;
  outline: none;
}
