  padding-left: 30px;
  padding-right: 5px;
  position: relative;
  height: 40px;
  border: none;
  outline: none;
  background-image: url("../img/svg/loupe-icon-black.svg");
  background-repeat: no-repeat;
  background-position: left center;
}
