    margin-right: 20px;
  }
}

.filter__submit {
  margin-bottom: 25px;
}

@media (max-width: 767px) {
  .filter--pop-up .filter__submit {
    margin-bottom: 20px;
  }
}

.filter__reset {
  margin-bottom: 20px;
}

.login {
  display: none;
}
.login--open {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.6);
}
@media (max-width: 1023px) {
  .login--open {
    background-color: #ffffff;
  }
}
.login--page {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.login__pop-up {
  width: 430px;
  min-height: 586px;
  box-sizing: border-box;
  background-color: #f0eae4;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
}
@media (max-width: 1023px) {
  .login__pop-up {
    width: 442px;
  }
}
@media (max-width: 767px) {
  .login__pop-up {
    width: 270px;
    min-height: 470px;
  }
}

.login__header {
  position: relative;
  min-height: 138px;
  margin-bottom: 50px;
  background-image: url("../img/jpg/login-modal-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .login__header {
    background-image: url("../img/jpg/login-modal-bg@2x.jpg");
  }
}
@media (max-width: 767px) {
  .login__header {
    min-height: 84px;
    margin-bottom: 37px;
  }
}

.login__close-button {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 7px;
  right: 7px;
  display: block;
  padding: 5px 0 0 0;
  box-sizing: border-box;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  outline: none;
  cursor: pointer;
}
.login__close-button:hover, .login__close-button:focus {
  border: 1px solid #1d1613;
}
.login__close-button:active {
  border: 1px solid #a87b62;
}
.login__close-button:active svg {
  fill: #a87b62;
}

.login--page .login__close-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}
@media (max-width: 1023px) {
  .login--page .login__close-button {
    top: 10px;
    right: 10px;
  }
}

@media (max-width: 767px) {
  .login--open .login__close-button {
    top: 2px;
    right: 2px;
  }
}

@media (max-width: 1023px) {
  .login__close-button svg {
    width: 20px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .login__close-button svg {
    width: 14px;
    height: 14px;
  }
}

.login__inner {
  padding-left: 45px;
  padding-right: 45px;
}
@media (max-width: 1023px) {
  .login__inner {
    padding-left: 51px;
    padding-right: 51px;
  }
}
@media (max-width: 767px) {
  .login__inner {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.login__pop-up h2 {
  font-size: 34px;
  line-height: 42.2px;
  margin-top: 0;
  margin-bottom: 25px;
  font-family: "Cormorant Garamond", "Times New Roman", serif;
  text-align: center;
}
@media (max-width: 767px) {
  .login__pop-up h2 {
    font-size: 24px;
    line-height: 31.2px;
    margin-bottom: 17px;
  }
}

.login__pop-up input {
  width: 100%;
  height: 40px;
  box-sizing: border-box;
  background-color: transparent;
  border: none;
  outline: none;
  border-bottom: 1px solid #1d1613;
}
.login__pop-up input::placeholder {
  font-size: 14px;
  line-height: 21px;
}
.login__pop-up input:hover, .login__pop-up input:focus {
  border-color: #a87b62;
}

.login__pop-up input[type=email] {
  margin-bottom: 25px;
}

@media (max-width: 767px) {
  .login--open input[type=email] {
    margin-bottom: 20px;
  }
}

.login__password-recovery {
  margin-bottom: 45px;
  padding-top: 4px;
  text-align: right;
}

@media (max-width: 767px) {
  .login--open .login__password-recovery {
    margin-bottom: 34px;
  }
}

.login__password-recovery a {
  font-size: 12px;
  line-height: 18px;
  margin-top: 10px;
  text-decoration: none;
  color: #9b9997;
}
.login__password-recovery a:hover {
  color: #a87b62;
}
.login__password-recovery a:active {
  color: rgba(168, 123, 98, 0.8);
}

.login__submit {
  width: 100%;
  margin-bottom: 15px;
}

.login__sign-up-link {
  font-size: 14px;
  line-height: 21px;
  padding-bottom: 30px;
  text-align: center;
}

.login__sign-up-link p {
  margin-top: 0;
  margin-bottom: 0;
  color: #9b9997;
}

.login__sign-up-link a {
  text-decoration: none;
  color: #1d1613;
}
.login__sign-up-link a:hover {
  color: #a87b62;
}
.login__sign-up-link a:active {
  color: rgba(168, 123, 98, 0.8);
}

.login__home-link {
  margin-bottom: 30px;
}

.login__logo {
  fill: #a87b62;
}
.login__logo:hover {
  fill: rgba(168, 123, 98, 0.8);
}
.login__logo:active {
  fill: #1d1613;
}